.top-nav--sticky {
  background: #093462;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.7); }

.top-nav {
  height: 90px;
  position: fixed;
  width: 100%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center; }
  .top-nav__unord-list {
    display: flex;
    margin: 0;
    padding: 0;
    height: inherit;
    align-items: center; }
    @media (min-width: 768px) {
      .top-nav__unord-list {
        width: 100%; } }
  .top-nav__list-item {
    margin: 0 20px;
    display: none; }
    @media (min-width: 768px) {
      .top-nav__list-item {
        display: block; } }
    .top-nav__list-item a {
      text-decoration: none;
      color: white;
      font: 400 20px "Quicksand";
      text-transform: uppercase;
      letter-spacing: 1px;
      cursor: pointer; }
      .top-nav__list-item a::after {
        content: "";
        display: block;
        width: 0;
        height: 1px;
        background: white;
        transition: width ease 0.5s; }
      .top-nav__list-item a:hover::after {
        width: 100%; }
  .top-nav__logo {
    margin: 0 auto 0 20px;
    border: 1px white solid;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center; }
    .top-nav__logo-link {
      color: white;
      padding: 0;
      font: 400 25px "Quicksand";
      text-decoration: none; }
  .top-nav__mobile-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 50px;
    width: 50px;
    border: 1px solid white;
    margin: 0 10px 0 auto;
    border-radius: 50%;
    font-size: 20px;
    color: white; }
    .top-nav__mobile-menu:hover {
      color: black;
      background: white; }
    @media (min-width: 768px) {
      .top-nav__mobile-menu {
        display: none; } }

.mobile-nav {
  display: none;
  position: fixed;
  top: 90px;
  height: 100vh;
  width: 100vw;
  background: whitesmoke;
  z-index: 50;
  transition: 300ms ease-out; }
  @media (max-width: 535px) {
    .mobile-nav {
      display: block; } }
  .mobile-nav__unord-list {
    padding: 0 0 90px;
    height: inherit;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; }
  .mobile-nav__list-item a {
    text-decoration: none;
    color: black;
    font: 400 30px "Quicksand";
    text-transform: uppercase;
    letter-spacing: 1px; }
    .mobile-nav__list-item a::after {
      content: "";
      display: block;
      width: 0;
