.hero {
  height: 100vh;
  width: 100%;
  background: url("https://i.imgur.com/YAin1uF.jpg") center center/cover no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative; }
